/* Основные переменные для темы */
:root {
  /* Светлая тема */
  --bg-primary: #ffffff;
  --bg-secondary: #f8f9fa;
  --text-primary: #1a1a1a;
  --text-secondary: #666666;
  --accent-primary: #007bff;
  --accent-hover: #0056b3;
  --border-color: #e0e0e0;
  --card-bg: #ffffff;
  --shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

[data-theme="dark"] {
  /* Тёмная тема */
  --bg-primary: #1a1a1a;
  --bg-secondary: #2d2d2d;
  --text-primary: #ffffff;
  --text-secondary: #b3b3b3;
  --accent-primary: #4dabf7;
  --accent-hover: #339af0;
  --border-color: #404040;
  --card-bg: #2d2d2d;
  --shadow: 0 2px 8px rgba(0, 0, 0, 0.3);
}

/* Базовые стили */
html {
  font-size: 16px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Articulat CF', -apple-system, BlinkMacSystemFont, sans-serif;
  font-weight: 700;
  background-color: var(--bg-primary);
  color: var(--text-primary);
  transition: all 0.3s ease;
  line-height: 1.6;
  font-size: 0.95rem;
}

.app {
  min-height: 100vh;
  background-color: var(--bg-primary);
}

/* Хедер */
.app-header {
  padding: 1.5rem 2rem;
  background-color: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.app-header h1 {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  font-size: 1.8rem;
  color: var(--text-primary);
  letter-spacing: -0.5px;
}

.theme-toggle {
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.5rem;
  color: var(--text-secondary);
  transition: color 0.3s ease;
}

.theme-toggle:hover {
  color: var(--accent-primary);
}

/* Навигационные вкладки */
.tabs {
  display: flex;
  background-color: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  padding: 0 2rem;
}

.tabs button {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  padding: 1rem 1.5rem;
  border: none;
  background: transparent;
  color: var(--text-secondary);
  cursor: pointer;
  transition: all 0.3s ease;
  border-bottom: 2px solid transparent;
  letter-spacing: -0.5px;
  font-size: 0.9rem;
  text-transform: uppercase;
}

.tabs button.active {
  color: var(--accent-primary);
  border-bottom-color: var(--accent-primary);
}

.tabs button:hover {
  color: var(--accent-primary);
}

/* Основной контент */
.content {
  padding: 2rem;
  max-width: 1200px;
  margin: 0 auto;
}

/* Компоненты */
.component {
  background-color: var(--card-bg);
  border-radius: 12px;
  padding: 2rem;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
}

.component h2 {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
  color: var(--text-primary);
  letter-spacing: -0.5px;
}

/* Календарь */
.calendar-container {
  background-color: var(--card-bg);
  border-radius: 12px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
  overflow: hidden;
}

.calendar-header {
  padding: 1.5rem;
  background-color: var(--bg-secondary);
  border-bottom: 1px solid var(--border-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.month-navigation {
  display: flex;
  align-items: center;
  gap: 1rem;
}

.month-navigation h2 {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  font-size: 1.3rem;
  color: var(--text-primary);
}

.nav-btn {
  background: var(--bg-secondary);
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 0.5rem;
  cursor: pointer;
  color: var(--text-secondary);
  transition: all 0.3s ease;
}

.nav-btn:hover {
  border-color: var(--accent-primary);
  color: var(--accent-primary);
}

/* Кнопки */
button {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  border: none;
  border-radius: 6px;
  padding: 0.75rem 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  letter-spacing: -0.5px;
}

.today-btn {
  background-color: var(--accent-primary);
  color: white;
}

.today-btn:hover {
  background-color: var(--accent-hover);
}

.task-input-container {
  display: flex;
  gap: 0.75rem;
  margin-top: 1rem;
}

.task-input-container input {
  flex: 1;
}

.add-task-btn {
  background: var(--accent-primary);
  color: #fff;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0.75rem;
}

.color-picker {
  display: flex;
  gap: 0.5rem;
  margin-top: 1rem;
}

.color-picker button {
  width: 1.5rem;
  height: 1.5rem;
  border-radius: 50%;
  border: 2px solid transparent;
  padding: 0;
}

.color-picker button.selected {
  border-color: var(--accent-primary);
}

/* Формы */
input, textarea, select {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
  border: 1px solid var(--border-color);
  border-radius: 6px;
  padding: 0.75rem;
  background-color: var(--bg-secondary);
  color: var(--text-primary);
  transition: all 0.3s ease;
}

input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 3px rgba(77, 171, 247, 0.1);
}

/* Календарь */
.calendar-body {
  display: flex;
  flex-direction: column;
}

.week-row {
  display: grid;
  grid-template-columns: repeat(7, minmax(0, 1fr));
}

.day-cell {
  min-height: 120px;
  padding: 0.75rem;
  border-right: 1px solid var(--border-color);
  border-bottom: 1px solid var(--border-color);
}

.day-cell.disabled {
  opacity: 0.35;
}

.day-cell.today {
  background-color: rgba(0, 123, 255, 0.08);
}

.day-cell.selected {
  outline: 2px solid var(--accent-primary);
}

.day-number {
  font-size: 0.95rem;
  margin-bottom: 0.5rem;
}

.day-tasks {
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.task-badge {
  width: 100%;
  padding: 0.25rem 0.4rem;
  border-radius: 4px;
  font-size: 0.75rem;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.more-tasks {
  font-size: 0.75rem;
  color: var(--text-secondary);
}

.selected-day-panel {
  padding: 1.5rem;
  border-top: 1px solid var(--border-color);
}

.task-list {
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-height: 320px;
  overflow-y: auto;
}

.task-item {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem;
  border-radius: 8px;
  background: var(--bg-secondary);
}

.task-text {
  flex: 1;
  font-size: 0.9rem;
}

.delete-btn {
  background: transparent;
  color: var(--text-secondary);
  padding: 0.25rem;
}

/* Notes */
.notes-container {
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 1.5rem;
}

.notes-sidebar {
  background: var(--bg-secondary);
  padding: 1.25rem;
  border-radius: 12px;
}

.new-note-btn {
  width: 100%;
  margin-bottom: 1rem;
  background: var(--accent-primary);
  color: #fff;
}

.notes-list {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  max-height: 400px;
  overflow-y: auto;
}

.note-item {
  position: relative;
  padding: 0.75rem;
  border-radius: 8px;
  background: var(--bg-primary);
  border: 1px solid var(--border-color);
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.note-item.active {
  border-color: var(--accent-primary);
  box-shadow: 0 0 0 2px rgba(0, 123, 255, 0.08);
}

.note-editor {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 1.5rem;
  border: 1px solid var(--border-color);
}

.title-input {
  width: 100%;
  font-size: 1.3rem;
  margin-bottom: 1rem;
}

.note-content {
  width: 100%;
  min-height: 320px;
  resize: vertical;
}

.empty-state {
  text-align: center;
  padding: 2rem;
}

.cta-button {
  margin-top: 1rem;
  background: var(--accent-primary);
  color: #fff;
}

/* Finance */
.finance-container {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.finance-summary {
  display: grid;
  grid-template-columns: repeat(3, minmax(0, 1fr));
  gap: 1rem;
}

.balance-card,
.income-card,
.expenses-card {
  background: var(--card-bg);
  border-radius: 12px;
  padding: 1.25rem;
  border: 1px solid var(--border-color);
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.balance-amount {
  font-size: 1.35rem;
}

.transaction-form {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.4rem;
}

.type-selector {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  gap: 0.5rem;
}

.type-btn {
  background: var(--bg-secondary);
  color: var(--text-secondary);
}

.type-btn.active {
  background: var(--accent-primary);
  color: #fff;
}

.transaction-history {
  background: var(--card-bg);
  padding: 1.5rem;
  border-radius: 12px;
  border: 1px solid var(--border-color);
}

.transactions-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  max-height: 360px;
  overflow-y: auto;
}

.transaction {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 0.75rem;
  padding: 0.85rem 1rem;
  border-radius: 10px;
  background: var(--bg-secondary);
}

.transaction-description {
  font-size: 0.9rem;
}

.transaction-amount span {
  font-weight: 700;
}

.transaction-amount .income {
  color: #28a745;
}

.transaction-amount .expense {
  color: #dc3545;
}

/* Адаптивность */
@media (max-width: 1024px) {
  .notes-container {
    grid-template-columns: 240px 1fr;
    gap: 1rem;
  }

  .finance-summary {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }

  .week-row {
    grid-template-columns: repeat(4, minmax(0, 1fr));
  }
}

@media (max-width: 768px) {
  .app-header {
    padding: 1rem;
  }
  
  .app-header h1 {
    font-size: 1.5rem;
  }
  
  .tabs {
    padding: 0 1rem;
  }
  
  .tabs button {
    padding: 0.75rem 1rem;
    font-size: 0.8rem;
  }
  
  .content {
    padding: 1rem;
  }
  
  .component {
    padding: 1.5rem;
  }

  .tabs {
    overflow-x: auto;
    gap: 0.5rem;
  }

  .tabs button {
    flex: 1;
    white-space: nowrap;
  }

  .calendar-container {
    overflow-x: auto;
  }

  .notes-container {
    grid-template-columns: 1fr;
  }

  .notes-list {
    max-height: none;
  }

  .finance-summary {
    grid-template-columns: 1fr;
  }

  .transaction-form {
    padding: 1rem;
  }

  .task-input-container {
    flex-direction: column;
  }

  .week-row {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 480px) {
  html {
    font-size: 14px;
  }

  .app-header {
    flex-direction: column;
    gap: 0.5rem;
    align-items: flex-start;
  }

  .tabs button {
    padding: 0.5rem;
    font-size: 0.7rem;
  }

  .calendar-header {
    flex-direction: column;
    gap: 0.75rem;
  }

  .selected-day-panel {
    padding: 1rem;
  }

  .task-item {
    flex-direction: column;
    align-items: flex-start;
  }

  .transaction {
    flex-direction: column;
    align-items: flex-start;
  }

  .transaction-amount {
    width: 100%;
    text-align: right;
  }
}

/* Анимации */
.fade-in {
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Утилиты */
.text-mono {
  font-family: 'Articulat CF', monospace;
  letter-spacing: -0.5px;
}

/* Специфические стили для компонентов */
.task-input input {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
}

.note-content {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
}

.transaction-form input,
.transaction-form select {
  font-family: 'Articulat CF', monospace;
  font-weight: 700;
}
